@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Architects+Daughter&display=swap);
body{font-family:"Akaya Telivigala", cursive;font-size:14px;background:#56baed;height:100vh}#root{height:50%;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.center-content-box{display:flex;justify-content:center}.game-info{margin-bottom:50px}.game-info ol,.game-info ul{padding-left:0}.game-info ol li,.game-info ul li{margin:10px}.game-info .status{margin-bottom:10px;min-width:170px}@media screen and (max-width: 575px){#root{transform:translate(-50%, -80%)}.board{width:240px;display:flex;flex-direction:column}.board section{display:flex}.board section .square{font-family:"Architects Daughter", cursive;background:#fff;border-radius:5px;font-size:80px;line-height:80px;margin:2px;text-align:center;height:80px;width:80px;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.board section .square:hover{opacity:0.9}.board section .square:active{opacity:0.8}}@media screen and (min-width: 476px){.board{width:420px;display:flex;flex-direction:column}.board section{display:flex}.board section .square{font-family:"Architects Daughter", cursive;background:#fff;border-radius:5px;font-size:140px;line-height:140px;margin:2px;text-align:center;height:140px;width:140px;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.board section .square:hover{opacity:0.9}.board section .square:active{opacity:0.8}}

